.user__name {
  margin: 0 20px 0 0;

  font-weight: 500;
  font-size: 42px;
  line-height: 1.15;
  text-transform: capitalize;


  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media (max-width:530px) {
  .user__name {
    font-size: 27px;
    margin: 0 10px 0 28px;
  }
}
